.map {
  width: 100%;
  height: 100vh;
}

.add {
  position: fixed;
  background: #65a836;
  color: white;
  padding: 10px 20px;
  z-index: 10;
  top: 20px;
  right: 20px;
  border-radius: 4px;
  cursor: pointer;
}

.faq {
  position: fixed;
  z-index: 10;
  left: 20px;
  top: 20px;
  background: #e0e0e0;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  border: 1px solid #ccc;
  cursor: pointer;
}
